.NavBar {
  height: 50px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.FlexAlign {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  background-color: #262727;
}

.FlexAlign a {
  color: white;
  font-size: 18px;
  font-weight: 600;
  padding: 15px 10px;
  background-color: #191a1a;
  border: 1px solid rgb(66, 66, 66);
  text-decoration: none;
  transition: 0.3s;
}

.FlexAlign a:hover {
  color: #000000;
  background-color: #ffffff;
  text-decoration: none;
  transition: 0.3s;
}