.phoneImg {
  width: 100%;
  height: auto;
}

.buttonFlex {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin-bottom: 50px;
}

@media only screen and (min-width: 620px) {
.buttonFlex a {
  color: rgb(231, 230, 229);
  text-decoration: none;
  font-size: 1rem;
  padding: 11px 90px;
  font-weight: bold;
  text-transform: uppercase;
  border-radius: 6px;
  border: 1px solid 91271d;
  background-color: #9e2d23;
  text-align: center;
  transition: 0.3s;
}}

@media only screen and (max-width: 619px) {
  .buttonFlex a {
    color: rgb(231, 230, 229);
    text-decoration: none;
    font-size: 18px;
    padding: 11px 9px;
    font-weight: bold;
    text-transform: uppercase;
    border-radius: 6px;
    border: 1px solid #91271d;
    background-color: #9e2d23;
    text-align: center;
    transition: 0.3s;
}}


.buttonFlex a:hover {
  color: #9e2d23;
  background-color: white;
  transition: 0.3s;
}

.PhoneCards .HrTwo {
  border-top: 1px solid rgb(65, 65, 65);
  border-bottom: 0px;
}

.GoldText {
  color: #ffd152;
}

.PhoneCards h2 {
  font-size: 38px;
  line-height: 36px;
  font-weight: 600;
  color: white;
  margin-bottom: 0px;
  padding-bottom: 0px;
}

.HrOne {
  padding-top: 0px;
  margin-top: 7px;
  border-bottom: 0px;
  border-top: 1px solid rgb(65, 65, 65);
}